
/*算法图*/

.graphCanvas svg g .node:hover {
    cursor: pointer;
}
.graphCanvas svg g  ellipse   {
  stroke: var(--font-color);
  fill: var(--font-color);
}
/*node label color*/
.graphCanvas svg g text {
  fill: var(--font-color);
}
/*图高亮
一种是改dot
一种是改css
            a33 [label="", xlabel="a33", color="orange",style=filled, fillcolor="yellow"];
*/

.graphCanvas svg g .lowlight ellipse {
  fill: gray;
  stroke: green;
}
.graphCanvas svg g .lowlight path {
  stroke: gray;
}
.graphCanvas svg g .lowlight polygon {
  fill: gray;
  stroke: green;
}

.graphCanvas svg g .prehigh ellipse {
  fill: aqua;
  stroke: green;
}
.graphCanvas svg g .prehigh path {
  stroke: aqua;
}
.graphCanvas svg g .prehigh polygon {
  fill: aqua;
  stroke: green;
}

.graphCanvas svg g .highlight ellipse {
  fill: yellow;
  stroke: orange;
}
.graphCanvas svg g .highlight path {
  stroke: orange;
}
.graphCanvas svg g .highlight polygon {
  fill: yellow;
  stroke: orange;
}

.graphCanvas svg g .posthigh ellipse {
  fill: #ccc;
  stroke: #aaa;
}
.graphCanvas svg g .posthigh path {
  stroke: #aaa;
}
.graphCanvas svg g .posthigh polygon {
  fill: #ccc;
  stroke: #aaa;
}

.graphCanvas svg g .first ellipse {
  fill: chartreuse;
  stroke: #aaa;
}
.graphCanvas svg g .first path {
  stroke: #aaa;
}
.graphCanvas svg g .first polygon {
  fill: chartreuse;
  stroke: #aaa;
}

.graphCanvas svg g .second ellipse {
  fill: red;
  stroke: #aaa;
}
.graphCanvas svg g .second path {
  stroke: #aaa;
}
.graphCanvas svg g .second polygon {
  fill: red;
  stroke: #aaa;
}

/*background color while transitioning*/
.graphCanvas svg g  path   {
  fill: var(--back-color);
}
.graphCanvas svg g .graph_node_table path   {/*border color without the outermost*/
  stroke: var(--font-color);
}