#code-overlay {
  position: absolute;
  top: 0;
  left: 0;

  background: rgba(0, 0, 0, 0.9);
  color: white;
  overflow: auto;
  border-radius: 0px;
  padding: 0px;
  z-index: 1000;
  opacity: 0.1;
}

#code-overlay.hidden {
  display: none;
}

#toggle-code {
  position: fixed;
  bottom: 10px;
  right: 10px;
  padding: 10px 20px;
  font-size: 16px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;

}

#toggle-code:hover {
  background-color: #0056b3;
}

#code-overlay pre {
  white-space: pre-wrap;
  word-wrap: break-word;
}
