/* src/components/Header.css */
.header {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: #333;
  color: white;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
  z-index: 1000;
}

.toggle {
  background: none;
  border: none;
  color: white;
  font-size: 24px;
  cursor: pointer;
  display: inline;
  -transform: translateX(60px);
  transition: transform 0.3s ease;
}
.toggle.open {
    -transform: translateX(250px);
}
.-toggle:hover {
  background-color: #555;
}

.title {
  font-size: 20px;
}

@media (max-width: 768px) {
  .toggle {
    display: inline;
  }
  .toggle.open {
      display: inline;
      -transform: translateX(60px);
    }
}
