/*sidebar*/
/* Sidebar styles */
.sidebar {
  position: fixed;
  top: 0;
  left: 0;
  width: 250px;
  height: 100%;
  background-color: #333;
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 20px;
  transform: translateX(-250px); /* Initially hidden transform: translateX(-190px); */
  transition: transform 0.3s ease;
  overflow-y: auto;
  z-index: 100; /* Ensure sidebar is above main content */
  margin-top:60px;
}

.sidebar.open {
  transform: translateX(0); /* Slide in when open */
}

/* Sidebar nav styles */
.-sidebar-nav {
  color: #fff;
  text-decoration: none;
  padding: 15px 20px;
  font-size: 18px;
  display: flex;
  align-items: center;
  width: 100%;
  transition: background-color 0.3s ease;
  cursor: pointer;
}
.sidebar-nav a, .sidebar-link {
  color: #fff;
  text-decoration: none;
  padding: 15px 20px;
  font-size: 18px;
  display: flex;
  align-items: center;
  width: 100%;
  transition: background-color 0.3s ease;
  cursor: pointer;
}

.sidebar-nav a:hover, .sidebar-link:hover {
  background-color: #444;
}

/* Icons styling */
.icon {
  margin-right: 10px;
}

/* Collapsible submenu */
.submenu {
  padding-left: 20px;
  display: flex;
  flex-direction: column;
}

.submenu a {
  padding: 10px 0;
  font-size: 16px;
  color: #aaa;
  text-decoration: none;
}

.submenu a:hover {
  color: #fff;
}

/* Chevron icon for submenu */
.chevron {
  margin-left: auto;
}


/* Responsive design for small screens */
@media (max-width: 768px) {
    .sidebar {
        width: 100%;
        transform: translateY(-120%);
    }

    .sidebar.open {
        transform: translateY(0);
    }
}

/*main*/

/* Adjust main content based on sidebar state */
.main {
    border: 1px solid red;
    --transform: translateX(-60px);
    --because-of-mui-db-margin-left:60px;
    transition: transform 0.3s ease;
    flex-grow: 1;
}

.main.open {
  -margin-left:250px;
  -transform: translateX(250px); /* Slide in when open */
}

@media (max-width: 768px) {
  .main {
    margin-left: 0; /* Reset margin on small screens */
  }
}