.full-screen {
    /*self*/
    position:absolute;
    border: 2px solid black;
    top:0;bottom:0;left:0;right:0;
    padding:0px;

    /*children*/
    display:flex;
    flex-direction: column;
}
.split-container {
    display: flex;

    /* Misc */
    border: 1px solid #efefef;/*#cbd5e0;*/
    height: 100%;
    width: 100%;
}

.split-resizer[data-direction='horizontal'] {
    background-color: #efefef;/*#cbd5e0;*/
    cursor: ew-resize;
    /*height: 100%;*/
    width: 8px;
    background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAUAAAAeCAYAAADkftS9AAAAIklEQVQoU2M4c+bMfxAGAgYYmwGrIIiDjrELjpo5aiZeMwF+yNnOs5KSvgAAAABJRU5ErkJggg==');
    background-repeat: no-repeat;
    background-position: 48%;
}
.split-resizer[data-direction='vertical'] {
    background-color: #efefef;/*#cbd5e0;*/
    cursor: ns-resize;
    height: 8px;
    /*width: 100%;*/
        background-image: url('hsplit.png');
        background-repeat: no-repeat;
        background-position: 48%;
}
.split-resizer:hover{
    background-color:#ddd;

}
.split-container__left {
    /* Initially, the left takes 1/4 width */
    width: 20%;

    /* Misc */
    //align-items: center;
    //display: flex;
    justify-content: center;


}
.split-container__right {
    /* Take the remaining width */
    flex: 1;

    /* Misc */
    align-items: stretch;/*center -> stretch*/
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.split-container__all {
    /* Initial height */
    height: 100%;

    /* Misc */
    align-items: center;
    display: flex;
    justify-content: flex-start;
}
.split-container__top {
    /* Initial height */
    height: 65%;

    /* Misc */
    align-items: center;
    display: flex;
    justify-content: flex-start;
}
.split-container__bottom {
    /* Take the remaining height */
    flex: 1;

    /* Misc */
    align-items: stretch;
    display: flex;
    justify-content: center;
}
.leftPanel {
    height:100%;

    /*for children*/
    display:flex;
    flex-direction:column; /*vertical flex*/
    justify-content:space-between; /*top and bottom*/
}
#searchPanel {
    height:100%;
    border: 0px solid red;
    /*for children*/
    display:flex;
    flex-direction:column; /*vertical flex*/
    justify-content:space-between; /*top and bottom*/
}
#searchBox {
    /*should be one line*/
    display:flex;
    justify-content:space-between;
    padding:4px;
}

#searchResult {
    /*border:1px black solid;*/
    flex:1;
    overflow-x:hidden;
    overflow-y:auto;
}
#page-selection{
    /*border:1px black solid;*/
    overflow:hidden;
}



.b-example-divider {
    height: 3rem;
    background-color: rgba(0, 0, 0, .1);
    border: solid rgba(0, 0, 0, .15);
    border-width: 1px 0;
    box-shadow: inset 0 .5em 1em rgba(0, 0, 0, .1), inset 0 .125em .5em rgba(0, 0, 0, .15);
}
.b-example-vr {
    flex-shrink: 0;
    width: 1rem;
    height: 100%;
}
