    /* General body and layout styles */
    * {
        box-sizing: border-box;
        margin: 0;
        padding: 0;
    }

    body {
        font-family: Arial, sans-serif;
        display: flex;
        flex-direction: column;
        height: 100vh;
        overflow: hidden;
        margin: 0;
        font-family: Arial, sans-serif;
        background-image: url('https://files.oaiusercontent.com/file-mFwOKAyZxhn2fItVg6YlL9lo?se=2024-10-08T12%3A40%3A55Z&sp=r&sv=2024-08-04&sr=b&rscc=max-age%3D604800%2C%20immutable%2C%20private&rscd=attachment%3B%20filename%3D2195a138-48a0-4945-a901-e6141e300ab6.webp&sig=tVhhyNrjWuRTpWmrKvGxrgIcqOfi2YyKd58Qtklv/O8%3D'); /* Replace with the correct path */
        background-size: cover; /* Cover the entire page */
        background-position: center; /* Center the background */
        color: #333; /* Text color */
    }

    #root {
        flex-grow: 1;
    }


  /* width */
    ::-webkit-scrollbar {
       width: 4px;
       height: 4px;
    }
  /* Track */
    ::-webkit-scrollbar-track {
      background: #f1f1f1;
   }

  /* Handle */
   ::-webkit-scrollbar-thumb {
      background: #bec4c4;
   }

  /* Handle on hover */
    ::-webkit-scrollbar-thumb:hover {
       background: #555;
   }

   .light {
       --back-color: white;
       --font-color: black;

   }
   .dark {
       --back-color: #121212;
       --font-color: white;
   }